import React from 'react';
import PropTypes from 'prop-types';
import SharedPropTypes from 'helpers/propTypes';

import Switch from 'react-switch';

import FocusGroup from 'focus/FocusGroup';
import Button from 'components/Button/_BUTTON_DEPRECATED';

import './ConsentSwitch.scss';

const ConsentSwitch = ({ backTo, checked, handleSwitch }) => (
    <FocusGroup>
        <Button className="ConsentSwitch" onClick={handleSwitch} id="switchButton" backTo={backTo}>
            <div className="ConsentText">
                <p>Data Collection</p>
            </div>
            <div className="SwitchContainer">
                <p>OFF</p>
                <Switch
                    className={`Switch ${checked ? 'checked' : ''}`}
                    data-testid="switch"
                    checked={checked}
                    onChange={() => {}}
                    uncheckedIcon={false}
                    checkedIcon={false}
                />
                <p>ON</p>
            </div>
        </Button>
    </FocusGroup>
);

ConsentSwitch.defaultProps = {
    backTo: null,
    checked: false,
    handleSwitch: () => {}
};

ConsentSwitch.propTypes = {
    backTo: SharedPropTypes.backTo,
    checked: PropTypes.bool,
    handleSwitch: PropTypes.func
};

export default ConsentSwitch;
