import { useEffect, useState } from 'react';
import axios from 'axios';
import useCookie from 'react-use-cookie';
import { useDevice } from 'contexts/DeviceContext';
import useFetch from 'hooks/useFetch';
import { getStorageValue, Keys } from 'utils/storage';

const devices = {
    freeview: 'freeview',
    youview: 'youview'
};

const consentResourceName = {
    [devices.freeview]: 'appZero',
    [devices.youview]: 'linear'
};

const COOKIE_EXPIRATION_DAYS = 30 * 6;
const cookieOptions = { days: COOKIE_EXPIRATION_DAYS };

const useLiveTV = () => {
    const device = useDevice();
    const deviceGroupName = device?.config?.deviceGroup;
    const url = device?.config?.resources?.[consentResourceName[deviceGroupName]];

    const [checked, setChecked] = useState(false);
    const [networkError, setNetworkError] = useState(false);
    const [dcCookie, setDcCookie] = useCookie('dc');

    const { data, loading } = useFetch(url, { responseType: 'text' });

    const getAppZeroConsentUrl = () => {
        const guid = getStorageValue(Keys.GUID);
        return device?.config?.services?.[consentResourceName[deviceGroupName]]?.replace(':itv-guid', guid);
    };

    useEffect(() => {
        async function fetchSavedData() {
            if (deviceGroupName === devices.freeview) {
                const appZeroConsentUrl = getAppZeroConsentUrl();
                const { data: response } = await axios.get(appZeroConsentUrl);
                const consentGiven = response.state?.consent?.consentState === 'on';
                setChecked(consentGiven);
            }
            if (deviceGroupName === devices.youview) {
                setChecked(dcCookie === '1');
            }
        }
        fetchSavedData().catch(() => setNetworkError(true));
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [deviceGroupName]);

    const handleSwitch = () => {
        switch (deviceGroupName) {
            case devices.freeview: {
                const appZeroConsentUrl = getAppZeroConsentUrl();
                axios
                    .post(appZeroConsentUrl, {
                        consent: {
                            consentState: checked ? 'off' : 'on',
                            lastAction: new Date().toISOString(),
                            version: '0.3',
                            status: 'done'
                        },
                        buildVersion: '0.9.23'
                    })
                    .then(() => {
                        setChecked(!checked);
                    })
                    .catch(() => {
                        setNetworkError(true);
                    });
                break;
            }
            case devices.youview: {
                const isChecked = dcCookie === '1';
                if (isChecked) {
                    setDcCookie('0', cookieOptions);
                } else {
                    setDcCookie('1', cookieOptions);
                }
                setChecked(!isChecked);
                break;
            }
            default:
                throw new Error('This hook can only be used with Freeview Play and YouView devices!');
        }
    };

    return {
        data,
        loading,
        checked,
        handleSwitch,
        networkError
    };
};

export default useLiveTV;
