import React, { memo, useState } from 'react';
import cn from 'classnames';
import { PageHeader } from 'components/Page';
import ScrollableText, { parseText } from 'components/ScrollableText';
import NoData from 'components/NoData';
import { PageTypes, PageIds } from 'utils/tracking';
import { grid, gridColumn, gridItem, gridItemAlignCenter, widthTwoThirds } from '@itv/fe-components/cjs/common/styles/grid.cls';
import SettingsPage from '../SettingsPage';
import ConsentSwitch from './ConsentSwitch';
import useLiveTV from '../hooks/useLiveTV';

import './LiveTv.scss';

const LiveTv = () => {
    const [content, setContent] = useState('');
    const [subtitle, setSubtitle] = useState('');
    const { data, loading, checked, handleSwitch, networkError } = useLiveTV();

    if (!content && data) {
        const parsed = parseText(data);
        setContent(parsed.content);
        setSubtitle(parsed.updated);
    }

    return (
        <SettingsPage id="liveTvPage" trackingPageType={PageTypes.GENERAL_INFO} trackingPageId={PageIds.LIVE_TV}>
            <div className={cn(grid, gridColumn, 'LiveTvContent')}>
                <div className={cn(gridItem, gridItemAlignCenter, widthTwoThirds, 'LiveTvContent')}>
                    {data && !networkError ? (
                        <>
                            <PageHeader title="Live TV" subtitle={subtitle} />
                            <ScrollableText content={content} backTo="live-tv-link" includeScrollBar />
                            <ConsentSwitch backTo="live-tv-link" checked={checked} handleSwitch={handleSwitch} />
                        </>
                    ) : (
                        <NoData loading={loading} />
                    )}
                </div>
            </div>
        </SettingsPage>
    );
};

export default memo(LiveTv);
